  grid-gap: var(--spacing-xl);
  grid-template-columns: auto auto 1fr; }

.footer__some-love {
  margin-bottom: var(--spacing-md); }

.footer .block__title {
  font-weight: bold;
  text-transform: uppercase;
  color: var(--text-color);
  margin-bottom: var(--spacing-xs); }

.main-layout--one-sidebar {
  display: grid;
  grid-gap: var(--spacing-xl);
  grid-template-columns: 1fr 280px; }

.region--content-header {
  background-color: #f3f4f9;
  --focus-box-shadow: 0 0 0 2px #f3f4f9, 0 0 0 5px var(--color-green); }

.region--content-header .region__container {
  padding-top: var(--spacing-xl);
  padding-bottom: var(--spacing-xl); }

.region--content-header .block--loremaster-breadcrumbs {
  margin-bottom: var(--spacing-md); }

.region--content {
  margin: var(--spacing-xl) 0; }

.region--content .block:not(:last-child) {
  margin-bottom: var(--spacing-lg); }

:root {
  --region-header-height: 50px; }

.region--header {
  background-color: #222330; }

.region--header :focus {
  --focus-box-shadow: 0 0 0 2px #222330, 0 0 0 5px var(--color-green); }

.region--header .region__container {
  display: grid;
  align-items: center;
  grid-gap: var(--grid-gap);
  grid-template-columns: 26px 1fr 15rem 2rem;
  height: var(--region-header-height); }

.region--sidebar-right {
  margin: var(--spacing-xl) 0; }

.region--sidebar-right .block:not(:last-child) {
  margin-bottom: var(--spacing-xl); }

.region--sidebar-right .block__title {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: var(--spacing-sm); }

.branding__logo {
  display: block; }

.block--druki-content-toc-mobile {
  display: none; }

:root {
  --druki-content-variations-active-background-color: rgba(0, 0, 0, 0.05);
  --druki-content-variations-border-color: var(--color-gray-blue);
  --druki-content-variations-label-icon: svg-load('drupal.svg', fill=#333); }

[data-theme="dark"] {
  --druki-content-variations-active-background-color: var(--color-light-gray);
  --druki-content-variations-border-color: var(--color-light-gray);
  --druki-content-variations-label-icon: svg-load('drupal.svg', fill=#fff); }

.druki-content-variations {
  align-items: center;
  border: 1px solid var(--druki-content-variations-border-color);
  border-radius: var(--radius-xs);
  display: flex;
  overflow: hidden;
  user-select: none; }

.druki-content-variations__label {
  align-items: center;
  display: flex;
  font-weight: bold;
  padding: var(--spacing-sm); }

.druki-content-variations__label::before {
  background-image: var(--druki-content-variations-label-icon);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  display: inline-block;
  height: 1rem;
  margin-right: var(--spacing-xs);
  width: 1rem; }

.druki-content-variations__variations {
  align-items: center;
  display: flex;
  margin-left: auto; }

.druki-content-variations__variation {
  border-left: 1px solid var(--druki-content-variations-border-color);
  display: block;
  padding: var(--spacing-sm); }

.druki-content-variations__variation:focus {
  box-shadow: inset 0 0 0 3px var(--color-green); }

.druki-content-variations__variation--active {
  background-color: var(--druki-content-variations-active-background-color);
  font-weight: bold; }

.header-dark-mode-switcher {
  width: 2rem;
  height: 2rem;
  background-image: svg-load("theme-light-dark.svg", fill=#FFF);
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  border-radius: var(--radius-xxs);
  background-color: transparent;
  border: none;
  align-self: center;
  display: flex; }

.header-dark-mode-switcher:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.druki-content-full h2,
.druki-content-full h3,
.druki-content-full h4,
.druki-content-full h5,
.druki-content-full h6 {
  margin-top: var(--spacing-xl);
  margin-bottom: var(--spacing-lg); }

.druki-content-full ol li,
.druki-content-full ul li,
.druki-content-full ol ol,
.druki-content-full ol ul,
.druki-content-full ul ul,
.druki-content-full ul ol {
  margin-top: var(--spacing-xxs);
  margin-bottom: var(--spacing-xxs); }

:root {
  --prismjs-background: var(--color-black-02);
  --prismjs-color: var(--color-black);
  --prismjs-keyword: hsl(240, 100%, 25%);
  --prismjs-punctuatuion: var(--prismjs-color);
  --prismjs-tag: #000080;
  --prismjs-string: #008000;
  --prismjs-comment: #808080;
  --pirsmjs-class-name: var(--color-black);
  --prismjs-variable: #660000;
  --prismjs-important: hsl(240, 100%, 25%);
  --prismjs-function: hsl(0, 0%, 0%);
  --prismjs-this: var(--prismjs-variable);
  --prismjs-constant: hsl(289, 79%, 27%);
  --prismjs-property: var(--prismjs-constant); }

[data-theme="dark"] {
  --prismjs-background: var(--color-white-02);
  --prismjs-color: hsl(211, 20%, 72%);
  --prismjs-important: hsl(27, 61%, 50%);
  --prismjs-keyword: var(--prismjs-important);
  --prismjs-comment: hsl(108, 28%, 46%);
  --prismjs-function: hsl(37, 100%, 71%);
  --prismjs-variable: hsl(280, 23%, 56%);
  --prismjs-tag: hsl(206, 38%, 57%);
  --prismjs-string: var(--prismjs-comment);
  --prismjs-constant: var(--prismjs-important); }

code[class*="language-"],
pre[class*="language-"] {
  font-family: var(--font-monospace);
  font-size: 14px;
  line-height: 1.4;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-wrap: normal;
  word-break: normal;
  tab-size: 4;
  hyphens: none;
  color: var(--prismjs-color);
  background: none; }

/* Code blocks */
pre[class*="language-"] {
  overflow: auto;
  margin: calc(var(--spacing-unit) * 3) 0; }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  border: 1px solid var(--color-primary-100);
  background-color: var(--prismjs-background); }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em; }

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: var(--prismjs-comment);
  /* base1 */ }

.token.punctuation {
  color: var(--prismjs-punctuatuion); }

.namespace {
  opacity: .7; }

.token.constant {
  color: var(--prismjs-constant); }

.token.property {
  color: var(--prismjs-property); }

.token.tag,
.token.boolean,
.token.number,
.token.symbol,
.token.deleted {
  color: var(--prismjs-tag); }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.url,
.token.inserted {
  color: var(--prismjs-string); }

.token.entity {
  color: #657b83;
  background: #eee8d5; }

.token.atrule,
.token.attr-value,
.token.keyword {
  color: var(--prismjs-keyword); }

.token.function {
  color: var(--prismjs-function); }

.token.class-name {
  color: var(--prismjs-class-name); }

.token.regex,
.token.variable {
  color: var(--prismjs-variable); }

.token.important,
.token.delimiter {
  color: var(--prismjs-important); }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }

.token.this {
  color: var(--prismjs-this); }

.main-navigation {
  display: flex;
  --link-color: var(--color-white);
  --link-color--hover: var(--color-white);
  font-size: var(--font-size-sm); }

.main-navigation__link {
  font-weight: bold;
  line-height: var(--region-header-height);
  position: relative;
  display: block;
  height: var(--region-header-height);
  padding: 0 var(--spacing-sm); }

.main-navigation__link::before {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.25rem;
  content: '';
  background-color: transparent; }

.main-navigation__link:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.main-navigation__link:hover::before,
.main-navigation__link.is-active::before {
  background-color: var(--color-blue); }

.paragraph-druki-code {
  border: 1px solid var(--color-light-gray);
  padding: var(--spacing-md);
  margin-bottom: var(--p-margin-bottom); }

.paragraph-druki-code pre {
  margin: unset; }

.druki-heading {
  scroll-margin-top: var(--spacing-xl);
  --link-color: var(--color-old-silver); }

.druki-heading__anchor {
  font-size: 80%;
  display: inline-block;
  margin-left: var(--spacing-xs); }

.druki-category-navigation__menu-item-link--active {
  color: var(--text-color);
  font-weight: bold; }

.druki-content-tabs__link {
  display: block; }

:root {
  --druki-content-toc-counter-color: var(--color-old-silver);
  --druki-content-toc-submenu-line-color: var(--color-light-gray); }

[data-theme="dark"] {
  --druki-content-toc-submenu-line-color: var(--color-light-gray); }

.druki-content-toc {
  font-size: var(--text-sm);
  line-height: 1.7; }

.druki-content-toc__menu {
  margin: unset;
  padding: unset; }

.druki-content-toc__link {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.druki-content-toc__link--active {
  font-weight: bold;
  color: var(--link-color); }

.druki-content-toc__submenu {
  position: relative; }

.druki-content-toc__submenu::before {
  position: absolute;
  left: calc(var(--spacing-sm) * -1);
  display: block;
  width: 2px;
  height: 100%;
  content: '';
  background-color: var(--druki-content-toc-submenu-line-color); }

.druki-content-toc ul {
  counter-reset: item; }

.druki-content-toc ul ul {
  margin: 0 0 0 var(--spacing-sm);
  padding-left: unset; }

.druki-content-toc li {
  display: block;
  vertical-align: top; }

.druki-content-toc li a::before {
  content: counters(item, ".") ". ";
  counter-increment: item;
  vertical-align: top;
  color: var(--druki-content-toc-counter-color); }

.druki-header-search {
  position: relative;
  display: flex; }

.druki-header-search__input {
  padding: 0 var(--spacing-xs);
  padding-left: calc(2rem + var(--spacing-xs));
  color: var(--color-white);
  border: unset;
  border-radius: var(--radius-xxs);
  background-color: rgba(255, 255, 255, 0.1);
  font-size: var(--font-size-xs);
  height: 2rem;
  width: 100%; }

.druki-header-search__input:hover {
  background-color: rgba(255, 255, 255, 0.15); }

.druki-header-search__submit {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 2rem;
  height: 2rem;
  border: unset;
  border-radius: var(--radius-xxs);
  background-color: transparent;
  background-image: svg-load("magnify.svg", fill=#ffffff);
  background-repeat: no-repeat;
  background-position: center; }
